<template>
	<view class="LaunchesPast">
		<view class="contant">
				
			<view class="cell-group">
				<view class="cell-title">{{detail.name}}</view>
				<view class="cell-date">
					任务编号：#{{detail.orbital_launch_attempt_count_year}}
				</view>
				<view class="cell-date">
					发射时间：<uni-dateformat :date="detail.net" format='yyyy年MM月dd日 hh时mm分ss秒'></uni-dateformat>
				</view>
				<view class="cell-details">
					{{detail.mission.description}}
				</view>
				<view class="cell-item">

				</view>
			</view>

			<view class="cell-group">
				<view class="cell-title">相关照片</view>
				<view class="cell-pic" >
					<image :src="detail.image" mode="widthFix"></image>
				</view>
				<!-- <view class="cell-date">图片较大，请耐心等待加载</view> -->
			</view>

		</view> 

	</view>
</template>

<script>
	export default {
		data() {
			return { 
				detail:'',
			};
		},
		onLoad(e) {
			this.detail=getApp().globalData.LaunchesUpcomingData[e.id];
		}
	}

</script>

<style lang="scss">
	.contant {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		.patch {
			height: 500rpx;
		}

		.cell-group {
			display: flex;
			flex-direction: column;
			width: 80%;
			background-color: #282828;
			border-radius: 30rpx;
			margin-bottom: 30rpx;

			.cell-title {
				padding: 30rpx 0rpx 0rpx 30rpx;
				color: #3978bb;
				font-size: 45rpx;
				font-weight: 800;
			}

			.cell-date {
				margin-left: auto;
				padding: 0rpx 20rpx 20rpx 0rpx;
				color: #7e7e7e;
				font-size: 15px;
				text-align: right;
			}

			.cell-details {
				padding: 30rpx;
				font-size: 30rpx;
				color: #fff;
			}

			.cell-pic {
				padding-top: 30rpx;
				image {
					width: 100%;
				}

			}

		}

